

const Symbols = ({symbols}) => {
    let data = []
    let keys = ['SymbolId', 'Symbol', 'Multiplier', 'FeeRatio', 'FundingRateCoefficient', 'Price', 'CumulativeFundingRate', 'TradersNetVolume', 'TradersNetCost']
    for (let i = 0; i < symbols[0].length; i++) {
        let d = [keys[i]]
        for (let j = 0; j < symbols.length; j++)
            d.push(symbols[j][i])
        data.push(d)
    }
    return (
        <div>
        <h3>Symbols</h3>
        <table border="1">
            {data.map(s =>
                (<tr>
                    {s.map(ss => <td>{ss}</td>)}
                </tr>)
            )}
        </table>
        </div>
    )
}

export default Symbols
